import React, { Component } from 'react';

import learn from '../../Images/learn-to-play.png';
import ranking from '../../Images/ranking.png';
import tournament from '../../Images/tournament.png';

import './style.css';

export default class Article extends Component {
    render() {
        return(
            <div className="article">
                <p className="col-xs-12 article-main-text">
                    Wszystko czego potrzebujesz, aby zacz gra w szachy
                </p>
                <div className="article-option">
                    <div className="col-xs-12 col-md-4 article-section">
                        <img src={learn} alt="learn"/>
                        <p className="article-text-title-option">
                            UCZ SI GRA
                        </p>
                        <p className="article-text-option">
                            Zapoznaj si z zasadmi - pokaemy Ci, jak si poruszaj pionki, przejrzyj kluczowe ruchy, ktre powiniene przemyle. Wszystko tu masz nawet jako niedowiadczony szachista.
                        </p>
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Ucz si gra
                            </span>
                        </div>
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Graj Online
                            </span>
                        </div>
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Graj z Komputerem
                            </span>
                        </div>
                    </div>
                    <div className="col-xs-12 col-md-4 article-section">
                        <img src={tournament} alt="tournament"/>
                        <p className="article-text-title-option">
                            TURNIEJE ANALIZA
                        </p>
                        <p className="article-text-option">
                            We udzia w turniejach midzy narodowych organizowanych kadego roku! Sprawd swoje umiejtnoci i wygraj tytu mistrza!
                        </p>                    
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Lista Turnieji
                            </span>
                        </div>
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Kolejny Turniej
                            </span>
                        </div>
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Zasady Turnieju
                            </span>
                        </div>
                    </div>
                    <div className="col-xs-12 col-md-4 article-section">
                        <img src={ranking} alt="ranking"/>
                        <p className="article-text-title-option">
                            RANKING
                        </p>
                        <p className="article-text-option">
                            Sprawd swj obecny ranking! Wspinaj si jak najwyej, osigaj wysoki poziom i poka umiejtnoci caemu wiatu!
                        </p>  
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Ranking Osb
                            </span>
                        </div>
                        <div className="article-check-options">
                            <span className="article-text glyphicon glyphicon-chevron-right"></span>
                            <span className="article-text">
                                Analiza Rozgrywek
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
} 


